.cards-holder {
  display: grid;
}

.cards-holder.triangular {
  margin-top: 8.05vw;
}

@media (max-width: 639px) {
  .cards-holder {
    grid-template-columns: 1fr;
    min-height: calc(100vh - 5vw - 2rem);
  }

  .cards-holder.triangular {
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 640px) {
  .cards-holder {
    margin-top: calc(50vh - 5.75rem - 6.4vw);
    grid-template-columns: 1fr 1fr;
  }

  .achievements {
    padding-bottom: calc(50vh - 3.75rem - 4.2vw);
  }

  .achievements.triangular {
    padding-bottom: 0;
  }
}

@media (min-width: 640px) and (max-width: 1199px) {
  .cards-holder {
    margin-top: calc(50vh - 5.75rem - 8.8vw);
  }

  .achievements {
    padding-bottom: calc(50vh - 3.75rem - 6.6vw);
  }
}

@media (min-width: 1200px) {
  .cards-holder {
    margin: calc(50vh - 5.75rem - 6.4vw) auto 0;
    max-width: 1400px;
  }

  .cards-holder.triangular {
    grid-template-columns: 1fr 1fr 1fr;
    margin: calc(50vh - 5.75rem - 6.4vw) 0 0;
  }

  .achievements {
    padding-bottom: calc(50vh - 3.75rem - 4.2vw);
  }
}



