.menu {
  position: fixed;
  bottom: 2.5vw;
  right: 6.25vw;
  width: 87.5vw;
  display: grid;
  grid-template-columns: 1fr 7.5vw;
  font-size: 4.5vw;
}

.toggle {
  text-align: center;
  text-decoration: none;
  background: #000;
  color: #eee;
  width: 7.5vw;
  height: 7.5vw;
  grid-column-start: 2;
  border-radius: 50%;
}

.menu.alt .toggle {
  background: #fff;
  -webkit-box-shadow: -1px -1px 1px #bdbdbd, 2px -1px 1px #bdbdbd;
          box-shadow: -1px -1px 1px #bdbdbd, 2px -1px 1px #bdbdbd;
}

.toggle::before {
  /* content: "";
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid #eee;
  position: absolute;
  top: 42px;
  left: calc(50vw - 4px); */
}

.menu.alt .toggle::before {
  border-top: 8px solid #212121;
}

.list {
  padding: 0;
  list-style: none;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.list li {
  -ms-flex-item-align: center;
      align-self: center;
  justify-self: center;
}

.list li a, .toggleHolder {
  padding: 1rem 2rem;
  text-decoration: none;
  color: #7CB342;
  background: #212121;
  -webkit-box-shadow: 2px 2px 2px #424242, -1px -1px 1px #424242, -1px 2px 1px #424242, 2px -1px 1px #424242;
          box-shadow: 2px 2px 2px #424242, -1px -1px 1px #424242, -1px 2px 1px #424242, 2px -1px 1px #424242;
  cursor: pointer;
}

.menu.alt .list li a, .menu.alt .toggleHolder {
  color: #212121;
  background: #eee;
  -webkit-box-shadow: 2px 2px 2px #bdbdbd, -1px -1px 1px #bdbdbd, -1px 2px 1px #bdbdbd, 2px -1px 1px #bdbdbd;
          box-shadow: 2px 2px 2px #bdbdbd, -1px -1px 1px #bdbdbd, -1px 2px 1px #bdbdbd, 2px -1px 1px #bdbdbd;
}

.list li a['disabled'] {
  pointer-events: none;
}

@media (max-width: 639px) {
  .list {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
  }

  .toggle {
    margin-top: 36vw;
    width: 15vw;
    height: 15vw;
  }

  .menu {
    right: 2.5vw;
    width: 95vw;
    grid-template-columns: 1fr 15vw
  }
}

@media (min-width: 640px) and (max-width: 1199px) {
  .list {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    font-size: 2vw;
  }

  .list li:not(.toggleHolder) {
    grid-row-start: 2
  }

  .toggle {
    margin-top: 10vw;
  }
}

@media (min-width: 1200px) {
  .list {
    font-size: 1.5vw;
  }
}



