.card {
  display: grid;
  grid-template-columns: 20% 80%;
  -webkit-box-shadow: 2px 2px 2px #424242, -1px -1px 1px #424242, -1px 2px 1px #424242, 2px -1px 1px #424242;
          box-shadow: 2px 2px 2px #424242, -1px -1px 1px #424242, -1px 2px 1px #424242, 2px -1px 1px #424242;
  margin: .5rem;
  cursor: pointer;
  background: #1a1a1a;
}

.cards-holder.triangular .card {
  /* 12vw 48vw 4vw medium */
  /* 0 72vw 8vw small */
  width: 60vw;
  height: 60vw;
  grid-template-columns: 1fr;
  text-align: center;
  margin: 0;
}

.cards-holder.triangular .card:nth-of-type(4n-2), .cards-holder.triangular .card:nth-of-type(3n) {
  -webkit-clip-path: polygon(0% 0%, 100% 0%, 50% 86.6%);
          clip-path: polygon(0% 0%, 100% 0%, 50% 86.6%);
  grid-template-rows: 10vw 20vw 21.96vw;
  padding: 0;
}

.cards-holder.triangular .card:nth-of-type(4n), .cards-holder.triangular .card:nth-of-type(4n+1) {
  -webkit-clip-path: polygon(0% 86.6%, 100% 86.6%, 50% 0);
          clip-path: polygon(0% 86.6%, 100% 86.6%, 50% 0);
  grid-template-rows: 21.96vw 20vw 10vw;
  padding: 0;
}

.cards-holder.triangular .card:nth-of-type(2n-1) {
  margin-left: 0;
  margin-right: -10vw;
}

.cards-holder.triangular .card:nth-of-type(2n) {
  margin-right: 0;
  margin-left: -10vw;
}

.cards-holder.triangular .card:nth-of-type(4n-2) .img-holder, .cards-holder.triangular .card:nth-of-type(3n) .img-holder {
  grid-row: 3 / 4;
}

.cards-holder.triangular .card:nth-of-type(4n) .img-holder, .cards-holder.triangular .card:nth-of-type(4n+1) .img-holder {
  grid-row: 1 / 2;
}

.cards-holder.triangular .card:nth-of-type(4n-2) h4, .cards-holder.triangular .card:nth-of-type(3n) h4 {
  grid-row: 1 / 2;
}

.cards-holder.triangular .card:nth-of-type(4n) h4, .cards-holder.triangular .card:nth-of-type(4n+1) h4 {
  grid-row: 3 / 4;
}

.cards-holder.triangular .card:nth-of-type(4n-2) .subheader .date, .cards-holder.triangular .card:nth-of-type(3n) .subheader .date {
  grid-row: 2 / 3;
}

.cards-holder.triangular .card:nth-of-type(4n) .subheader .date, .cards-holder.triangular .card:nth-of-type(4n+1) .subheader .date {
  grid-row: 1 / 2;
}

.cards-holder.triangular .card .subheader {
  display: grid;
}

.cards-holder.triangular .card .subheader .date {
  max-width: 100%;
  margin: auto;
}

.card.alt {
  -webkit-box-shadow: 2px 2px 2px #bdbdbd, -1px -1px 1px #bdbdbd, -1px 2px 1px #bdbdbd, 2px -1px 1px #bdbdbd;
          box-shadow: 2px 2px 2px #bdbdbd, -1px -1px 1px #bdbdbd, -1px 2px 1px #bdbdbd, 2px -1px 1px #bdbdbd;
  background: #e8e8e8;
}

.card:active, .card:hover, .card:focus {
  background: #000;
}

.card.alt:active, .card.alt:hover, .card.alt:focus {
  background: #fff;
}

.card h4, .card p {
  font-size: 4vw;
  line-height: 5vw;
}

.cards-holder.triangular .card h4, .cards-holder.triangular .card p {
  font-size: 3vw;
  line-height: 4vw;
}

.card h4 {
  margin: .5rem 0 .25rem;
}

.card p {
  margin: .25rem 0 .5rem;
}

.card p .date {
  font-family: 'Ubuntu+Mono', monospace;
  border: 1px solid rgba(255, 255, 255, 0.4);
  padding: 0 .25rem;
  margin-right: .5rem;
}

.card.alt p .date {
  border: 1px solid rgba(0, 0, 0, 0.4);
}

.card p .date:empty {
  border: none;
  padding: 0;
  margin: 0
}

.cards-holder.triangular .card h4, .cards-holder.triangular .card p span {
  margin: auto;
}

.img-holder {
  grid-row: 1 / 3;
  position: relative;
}

.img-holder img {
  max-width: 7vw;
  max-height: 7vw;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-filter: drop-shadow(.1vw .1vw .2vw #bdbdbd) drop-shadow(-.1vw .1vw .2vw #bdbdbd);
          filter: drop-shadow(.1vw .1vw .2vw #bdbdbd) drop-shadow(-.1vw .1vw .2vw #bdbdbd);
}

.cards-holder.triangular .card:nth-of-type(4n-2) .img-holder img, .cards-holder.triangular .card:nth-of-type(3n) .img-holder img {
  top: 25%;
}

.cards-holder.triangular .card:nth-of-type(4n) .img-holder img, .cards-holder.triangular .card:nth-of-type(4n+1) .img-holder img {
  top: 75%;
}

.locked {
  color: #EF9A9A;
  background: #333;
  cursor: auto;
}

.locked.alt {
  color: #E53935;
  background: #bdbdbd;
}

.locked:active, .locked:hover, .locked:focus {
  background: #333;
}

.locked.alt:active, .locked.alt:hover, .locked.alt:focus {
  background: #bdbdbd;
}

.future {
  color: #B39DDB;
  background: #333;
  cursor: auto;
}

.future.alt {
  color: #8E24AA;
  background: #e2e2e2;
}

.future:active, .future:hover, .future:focus {
  background: #1a1a1a;
}

.future.alt:active, .future.alt:hover, .future.alt:focus {
  background: #f2f2f2;
}

@media (min-width: 640px) {
  .img-holder img {
    max-width: 3vw;
    max-height: 3vw;
  }

  .future p {
    line-height: 0;
    margin: 0;
  }

  .future h4 {
    margin-top: 1.25rem;
  }
}

@media (min-width: 640px) and (max-width: 1199px) {
  .card:nth-of-type(2n) {
    margin-right: 2.5rem;
  }

  .card:nth-of-type(2n+1) {
    margin-left: 2.5rem;
  }

  .card h4, .card p, .cards-holder.triangular .card h4, .cards-holder.triangular .card p {
    font-size: 1.8vw;
    line-height: 2.2vw;
  }

  .cards-holder.triangular .card {
    /* 12vw 48vw 4vw medium */
    /* 0 60vw 10vw small */
    width: 48vw;
    height: 48vw;
  }

  /* 0.625 */
  .cards-holder.triangular .card:nth-of-type(4n-2), .cards-holder.triangular .card:nth-of-type(3n) {
    grid-template-rows: 8vw 16vw 17.568vw;
  }

  .cards-holder.triangular .card:nth-of-type(4n), .cards-holder.triangular .card:nth-of-type(4n+1) {
    grid-template-rows: 17.568vw 16vw 8vw;
  }

  .cards-holder.triangular .card:nth-of-type(n+3) {
    margin-top: -3.432vw;
  }

  .cards-holder.triangular .card:nth-of-type(2n-1) {
    margin-left: 12vw;
    margin-right: -11vw;
  }

  .cards-holder.triangular .card:nth-of-type(2n) {
    margin-right: 12vw;
    margin-left: -11vw;
  }
}

@media (min-width: 1200px) {
  .card:nth-of-type(2n) {
    margin-right: 2.5rem;
  }

  .card:nth-of-type(2n+1) {
    margin-left: 2.5rem;
  }

  .cards-holder.triangular {
    min-height: calc(100vh - 10.05vw - 2rem);
  }

  .card h4, .card p, .cards-holder.triangular .card h4, .cards-holder.triangular .card p {
    font-size: 1.2vw;
    line-height: 1.4vw;
  }

  .cards-holder.triangular .card {
    /* 12vw 48vw 4vw medium */
    /* 0 60vw 10vw small */
    width: 30vw;
    height: 30vw;
  }

  .cards-holder.triangular .card:nth-of-type(2n) {
    grid-template-rows: 5vw 10vw 10.98vw;
    -webkit-clip-path: polygon(0% 0%, 100% 0%, 50% 86.6%);
            clip-path: polygon(0% 0%, 100% 0%, 50% 86.6%);
  }

  .cards-holder.triangular .card:nth-of-type(2n+1) {
    grid-template-rows: 10.98vw 10vw 5vw;
    -webkit-clip-path: polygon(0% 86.6%, 100% 86.6%, 50% 0);
            clip-path: polygon(0% 86.6%, 100% 86.6%, 50% 0);
  }

  .cards-holder.triangular .card:nth-of-type(2n) .img-holder {
    grid-row: 3 / 4;
  }

  .cards-holder.triangular .card:nth-of-type(2n+1) .img-holder {
    grid-row: 1 / 2;
  }

  .cards-holder.triangular .card:nth-of-type(2n) h4 {
    grid-row: 1 / 2;
  }

  .cards-holder.triangular .card:nth-of-type(2n+1) h4 {
    grid-row: 3 / 4;
  }

  .cards-holder.triangular .card:nth-of-type(2n) .subheader .date {
    grid-row: 2 / 3;
  }

  .cards-holder.triangular .card:nth-of-type(2n+1) .subheader .date {
    grid-row: 1 / 2;
  }

  .cards-holder.triangular .card:nth-of-type(2n) .img-holder img {
    top: 25%;
  }

  .cards-holder.triangular .card:nth-of-type(2n+1) .img-holder img {
    top: 75%;
  }

  .cards-holder.triangular .card:nth-of-type(3n+2) {
    margin-left: -5vw;
    margin-right: -5vw;
  }

  .cards-holder.triangular .card:nth-of-type(6n-4) {
    margin-top: -3.05vw;
  }

  .cards-holder.triangular .card:nth-of-type(n+3) {
    margin-top: .98vw;
  }

  .cards-holder.triangular .card:nth-of-type(6n-3) {
    margin-top: 0;
  }

  .cards-holder.triangular .card:nth-of-type(6n-1) {
    margin-top: 4.03vw;
  }

  .cards-holder.triangular .card:nth-of-type(3n+1) {
    margin-left: 15vw;
    margin-right: -5vw;
  }

  .cards-holder.triangular .card:nth-of-type(3n) {
    margin-right: 15vw;
    margin-left: -5vw;
  }
}



