.prompt {
  font-family: 'Ubuntu+Mono', monospace;
  font-size: 4vw;
  line-height: 5vw;
  word-break: keep-all;
  text-align: left;
  padding: 1rem;
  margin: 0 auto;
  background: #171717;
  max-width: 1400px;
}

.prompt.alt {
  background: #e0e0e0;
}

.prompt span {
  color: #eee;
}

.prompt.alt span {
  color: #212121;
}

@media (min-width: 640px) {
  .prompt {
    font-size: 1.5vw;
    line-height: 2vw;
  }
}



